<template>
    <div class="wrap1">
          <div class="register">
      <div class="register-wrapper">
        <div class="register-title">
           承运人注册
        </div>
      <el-form :model="ruleForm"  :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <el-form-item label="手机号" prop="number" class="lable">
          <el-input v-model="ruleForm.number"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass" class="lable">
          <el-input type="password" v-model="ruleForm.pass"></el-input>
        </el-form-item>
        <el-form-item label="确认密码" prop="checkPass" class="lable">
          <el-input type="password" v-model="ruleForm.checkPass"></el-input>
        </el-form-item>
        <el-form-item label="手机验证码" prop="yz" class="lable">
          <el-input ></el-input>
        </el-form-item>
        <div class="register-title">
          企业信息
        </div>
        <el-form-item label="企业名称" prop="username" class="lable">
          <el-input v-model="ruleForm.username"></el-input>
        </el-form-item>
        <el-form-item label="企业地址" prop="address" class="lable">
          <el-input v-model="ruleForm.address"></el-input>
        </el-form-item>
        <el-form-item label="负责人" prop="personal" class="lable">
          <el-input v-model="ruleForm.personal"></el-input>
        </el-form-item>
        <el-form-item label="联系手机" prop="phone" class="lable">
          <el-input v-model="ruleForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="传真" prop="" class="lable">
          <el-input ></el-input>
        </el-form-item>
        <el-form-item label="运营证书" prop="zs" class="lable">
          <el-input  v-model="ruleForm.zs"></el-input>
        </el-form-item>

        <el-form-item label="运营执照" prop="zz" class="lable">
          <el-input v-model="ruleForm.zz"></el-input>
        </el-form-item>
        <el-form-item label="其他资质证书"  class="lable">
          <el-input ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="form.resource" class="register-radio">
            <el-radio label="fd">阅读并同意<span>《软件许可及服务协议》</span></el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item class="register-button">
          <router-link to="/index" tag="el-button" class="register-subbtn" type="primary" @click="submitForm('ruleForm')">注册</router-link>
        </el-form-item>
      </el-form>
      </div>
    </div>
    </div>
</template>
<script>
    export default {
      data() {
        var validatePass = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请输入密码'));
          } else {
            if (this.ruleForm2.pass !== '') {
              this.$refs.ruleForm2.validateField('pass');
            }
            callback();
          }
        };
        var validatePass2 = (rule, value, callback) => {
          if (value === '') {
            callback(new Error('请再次输入密码'));
          } else if (value !== this.ruleForm2.pass) {
            callback(new Error('两次输入密码不一致!'));
          } else {
            callback();
          }
        };
        return {
          ruleForm: {
            yz:'',
            pass: '',
            phone:'',
            zs:'',
            zz:'',
            address:'',
            phone:'',
            personal:'',
            name:'',
            checkPass: '',
            number: '',
            username:'',
            region: '',
            date1: '',
            date2: '',
            delivery: false,
            type: [],
            resource: '',
            desc: ''
          },
          form: {
            name: '',
            region: '',
            date1: '',
            date2: '',
            delivery: false,
            type: [],
            resource: '',
            desc: ''
          },
          rules: {
            pass: [
              { required: true,validator: validatePass, trigger: 'blur' }
            ],
            checkPass: [
              { required: true,validator: validatePass2, trigger: 'blur' }
            ],
            number: [
              { required: true, message: '请输入活动名称', trigger: 'blur' },
              { min: 11,max:11, message: '手机号必须11位数字', trigger: 'blur' }
            ],
            name: [
              { required: true, message: '请输入企业名称', trigger: 'blur' },
             { min: 3,max:5, message: '必须3至5位字母', trigger: 'blur' }
            ],
            address: [
              { required: true, message: '请输入企业地址', trigger: 'blur' }
            ],
            personal: [
              { required: true, message: '请输入负责人', trigger: 'blur' }
            ],
            phone: [
              { required: true, message: '请输入联系人电话', trigger: 'blur' },
              { min: 11,max:11, message: '手机号必须11位数字', trigger: 'blur' }
            ],
            zs: [
              { required: true, message: '请上传证书',trigger: 'blur' }
            ],
            zz: [
              { required: true,message: '请上传执照', trigger: 'blur' }
            ],
            yz: [
              { required: true,message: '请上输入验证码', trigger: 'blur' }
            ],
            username:[
              { required: true, message: '请输入企业名称', trigger: 'blur' },
             { min: 3,max:5, message: '必须3至5位字母', trigger: 'blur' }
            ]
          }
        };
      },
      methods: {
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              alert('submit!');
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
        resetForm(formName) {
          this.$refs[formName].resetFields();
        }
      }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.wrap1{
  width: 100%;
  height: 100%;
  background: #F5F6FB;
  padding-top: 30px;
  border-top: 1px solid #E9E9F3;
  .register{
  width:1200px;
  // margin:29px 0px 0px 200px ;
  margin: 0 auto ;
  background-color:#fff;
  border: 2px solid #e7e9f5;
  .register-wrapper{
    width:414px;
    margin:0 auto;
    text-align: center;
  .register-title{
      padding:20px 0px;
      color:#2c5396;
    }
  }
  .register-button{
    .register-subbtn{
      background-color:#f45c44;
      border:1px solid #f45c44;
      width:100%;
      height:100%;
      color:#fff;
    }
  }
  .register-radio{
    width:100%;
    height:100%;
    text-align: left;
    span{
      color: #5373c6;
    }
  }
}
}
</style>



